
.opui-scroll-ctrl-content {
  overflow: hidden;
}

.op-calendar-pc {
  margin: 0 -16px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
}

.op-calendar-pc-box {
  height: 472px;
  position: relative;
  z-index: 1;
}

.op-calendar-pc-box .c-dropdown2 {
  height: 30px;
  line-height: 30px;
}
.op-calendar-pc-box .c-dropdown2 .c-dropdown2-menu {
  top: 35px;
  border: none;
  border-radius: 6px;
  padding: 8px 0;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
}
.op-calendar-pc-box .c-dropdown2 .c-dropdown2-menu .opui-scroll-ctrl-scroll {
  top: 10px;
  width: 6px;
  border-top: none;
  border-left: 2px solid #f5f5f6;
}
.op-calendar-pc-box
  .c-dropdown2
  .c-dropdown2-menu
  .opui-scroll-ctrl-scroll-hover {
  border-bottom: none;
}
.op-calendar-pc-box
  .c-dropdown2
  .c-dropdown2-menu
  .opui-scroll-ctrl-scroll-touch {
  border-right: none;
  border-bottom: none;
}
.op-calendar-pc-box
  .c-dropdown2
  .c-dropdown2-menu
  .opui-scroll-ctrl-scroll
  .opui-scroll-axis {
  width: 6px;
  background: transparent;
}
.op-calendar-pc-box
  .c-dropdown2
  .c-dropdown2-menu
  .opui-scroll-ctrl-scroll
  .opui-scroll-slider {
  margin-left: -2px;
  width: 2px;
  border: none;
  background: #4e6ef2;
  border-radius: 1px;
}
.op-calendar-pc-box .c-dropdown2 .c-dropdown2-option.c-dropdown2-selected {
  background-color: transparent;
  color: #315efb;
}
.op-calendar-pc-box .c-dropdown2 .c-dropdown2-btn-group {
  height: 100%;
  border-radius: 6px;
  border: 1px solid #d7d9e0;
  box-sizing: border-box;
  box-shadow: none;
  overflow: hidden;
}
.op-calendar-pc-box .c-dropdown2 .c-dropdown2-btn-group:hover {
  border: 1px solid #9195a3;
}
.op-calendar-pc-box .c-dropdown2 .c-dropdown2-btn {
  position: relative;
  height: 100%;
}
.op-calendar-pc-box .c-dropdown2 .c-dropdown2-btn-icon {
  width: 34px;
  height: 28px;
  line-height: 28px;
  padding: 0;
  background-color: transparent;
  box-shadow: none;
  -webkit-box-shadow: none;
  cursor: pointer;
}
.op-calendar-pc-box .c-dropdown2 .c-dropdown2-btn-icon-border {
  width: 34px;
  height: 100%;
  border-left: none;
  background-color: transparent;
}
.op-calendar-pc-box
  .c-dropdown2
  .c-dropdown2-btn-icon-border
  .c-icon-triangle-down {
  background: url(//www.baidu.com/aladdin/tpl/calendar_pc/arrow_down.2eed9b1d.png)
    no-repeat center;
  background-size: 16px auto;
}

.op-calendar-pc-select-box {
  height: 30px;
  zoom: 1;
  visibility: hidden;
}

.op-calendar-pc-year-box,
.op-calendar-pc-month-box,
.op-calendar-pc-holiday-box {
  float: left;
  margin-right: 9px;
  color: #333;
  min-width: 80px;
}

.op-calendar-pc-year-box {
  width: 120px;
}

.op-calendar-pc-month-box {
  position: relative;
  width: 80px;
  padding: 0 34px;
  z-index: 1;
}

.op-calendar-pc-holiday-box {
  width: 96px;
}

.op-calendar-pc-backtoday {
  float: right;
  margin-right: 2px;
  width: 68px;
  height: 38px;
  line-height: 30px;
  text-align: center;
  background: #f5f5f6;
  border-radius: 6px;
  color: #333;
  cursor: pointer;
  padding: 4px;
  font-size: 12px;
  box-sizing: border-box;
}
.op-calendar-pc-backtoday:hover {
  color: #315efb;
  background: #f0f0f1;
}
.op-calendar-pc-holidaystyle .op-calendar-pc-backtoday:hover {
  color: #f73131;
  background: #f0f0f1;
}

.op-calendar-pc-left {
  height: 100%;
  padding: 15px 15px 0;
  float: left;
  border-radius: 16px 0 0 16px;
  border: 2px solid #4e6ef2;
  border-right: none;
  box-sizing: border-box;
}

.op-calendar-pc-table-box {
  margin-top: 14px;
}

.op-calendar-pc-month-box .c-dropdown2-btn-icon-border {
  border-color: transparent;
  _border-color: transparent;
  background-color: transparent !important;
}

.op-calendar-pc .c-dropdown2 .c-dropdown2-btn-icon {
  padding-left: 0;
}

.op-calendar-pc-prev-month,
.op-calendar-pc-next-month {
  position: absolute;
  top: 0;
  display: block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  color: #9195a3;
  font-size: 16px;
  cursor: pointer;
}

.op-calendar-pc-prev-month {
  left: 0;
  background: url(//www.baidu.com/aladdin/tpl/calendar_pc/arrow_left.6742b04d.png)
    no-repeat center;
  background-size: 16px auto;
}

.op-calendar-pc-next-month {
  right: 0;
  background: url(//www.baidu.com/aladdin/tpl/calendar_pc/arrow_right.eb28ae9c.png)
    no-repeat center;
  background-size: 16px auto;
}

.op-calendar-pc-table {
  border-collapse: collapse;
  border-spacing: 0;
}

.op-calendar-pc-table th {
  width: 62px;
  height: 36px;
  line-height: 36px;
  font-weight: normal;
  font-size: 14px;
}

.op-calendar-pc-table td {
  padding: 2px;
}

.op-calendar-pc-table td a {
  display: block;
  width: 60px;
  height: 56px;
  padding-top: 8px;
  text-align: center;
  text-decoration: none;
  line-height: 1;
  white-space: nowrap;
  border: 2px solid transparent;
  border-radius: 6px;
  overflow: hidden;
  box-sizing: border-box;
  cursor: pointer;
}

.op-calendar-pc-relative {
  position: relative;
  zoom: 1;
}

.op-calendar-pc-relative .op-calendar-pc-table-selected,
.op-calendar-pc-relative a:hover {
  border: 2px solid #bdbfc8;
}

.op-calendar-pc-table-rest.op-calendar-pc-table-selected,
.op-calendar-pc-table-rest:hover {
  border: 2px solid #f38686 !important;
}

.op-calendar-pc-daynumber {
  display: block;
  height: 22px;
  font-size: 18px;
  color: #000;
}

.op-calendar-pc-table-almanac {
  display: block;
  color: #333;
  font-size: 12px;
}

.op-calendar-pc-table-weekend .op-calendar-pc-daynumber,
.op-calendar-pc-table-festival .op-calendar-pc-table-almanac,
.op-calendar-pc-table-rest .op-calendar-pc-daynumber {
  color: #f73131;
}

.op-calendar-pc-table-other-month {
  filter: alpha(opacity=40);
  opacity: 0.4;
}

.op-calendar-pc-table-today {
  border: 2px solid #4e6ef2 !important;
}

.op-calendar-pc-table-rest {
  background: rgb(247, 49, 49, 0.15);
}

.op-calendar-pc-table-work {
  background: #f5f5f6;
}

.op-calendar-pc-table-holiday-sign {
  position: absolute;
  left: 4px;
  top: 4px;
  color: #f73131;
  font-size: 12px;
  line-height: 12px;
  *line-height: 12px;
}

.op-calendar-pc-table-work .op-calendar-pc-daynumber {
  color: #000;
}

.op-calendar-pc-table-work .op-calendar-pc-table-holiday-sign {
  color: #626675;
}

.op-calendar-pc-right {
  height: 100%;
  margin-left: 480px;
  color: #fff;
  text-align: center;
  border-radius: 0 16px 16px 0;
  background: #4e6ef2;
}

.op-calendar-pc-right-date {
  line-height: 45px;
  font-size: 13px;
}

.op-calendar-pc-right-day {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto;
  line-height: 80px;
  font-size: 52px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 12px;
}

.op-calendar-pc-right-lunar span {
  display: block;
}

.op-calendar-pc-right-holid1 {
  position: relative;
  margin-top: 13px;
  padding-left: 22px;
  padding-right: 14px;
  text-align: justify;
  color: #fff;
  font-size: 12px;
  line-height: 16px;
}

.op-calendar-pc-right-holid2 {
  position: relative;
  margin-top: 13px;
  padding-left: 22px;
  padding-right: 14px;
  text-align: justify;
  color: #fff;
  font-size: 12px;
  line-height: 16px;
}

.op-calendar-pc-right-holid1::before,
.op-calendar-pc-right-holid2::before {
  content: "";
  position: absolute;
  top: 6px;
  left: 16px;
  width: 3px;
  height: 3px;
  background: #fff;
  border-radius: 50%;
}

.op-calendar-pc-right-almanacbox {
  position: relative;
  margin-top: 12px;
  padding-top: 12px;
  padding-bottom: 100px;
  margin-bottom: -100px;
  background: rgba(255, 255, 255, 0.15);
}

.op-calendar-pc-right-almanac {
  font-size: 12px;
  line-height: 20px;
  overflow: hidden;
}

.op-calendar-pc-right-suit {
  display: inline-block;
  width: 50%;
  float: left;
}

.op-calendar-pc-right-avoid {
  display: inline-block;
  width: 50%;
  float: right;
}

.op-calendar-pc-right-almanac i {
  display: block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin: 0 auto;
  font-style: normal;
  font-size: 24px;
  color: #fff;
}

.op-calendar-pc-right-hover .op-calendar-hover-almanac {
  display: block;
}

.op-calendar-hover-almanac {
  display: none;
  position: absolute;
  z-index: 100;
  width: 190px;
  top: 0;
  right: -230px;
  background: #fff;
  padding: 16px;
  color: #333;
  font-size: 13px;
  border-radius: 6px;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
}

.op-calendar-hover-suit,
.op-calendar-hover-avoid {
  padding-left: 34px;
  position: relative;
  display: block;
  min-height: 30px;
  text-align: left;
}

.op-calendar-hover-avoid {
  margin-top: 10px;
}

.op-calendar-hover-suit i,
.op-calendar-hover-avoid i {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  color: #fff;
  font-style: normal;
  font-size: 18px;
  background: #4e6ef2;
  border-radius: 6px;
}

.op-calendar-hover-avoid i {
  background: #f73131;
}

.op-calendar-pc-holidaytip {
  display: none;
  position: relative;
  background: #f7f7f7;
  padding: 10px 10px 10px 0;
}

.op-calendar-pc-holidaytip p {
  margin-left: 35px;
}

.op-calendar-pc-holidaytip-icon {
  position: absolute;
  left: 0;
  top: 10px;
  padding-left: 10px;
  width: 20px;
  height: 20px;
  text-align: center;
  font: 20px/20px Simsun;
  color: #61b0ff;
}

.op-calendar-pc-holidaytip-icon i {
  font-style: normal;
  font: 14px/20px Tahoma, Arial;
  position: absolute;
  width: 20px;
  height: 20px;
  right: 0;
  top: 0;
}

.op-calendar-pc-holidaystyle .op-calendar-pc-left {
  border-color: #eb3636;
}

.op-calendar-pc-holidaystyle .op-calendar-pc-right {
  /*filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#cb1c18, endColorstr=#f44f23, grandientType=0);
    background: -moz-linear-gradient(top, #cb1c18, #f44f23);*/
  background: #eb3636;
}

.op-calendar-pc-holidaystyle .op-calendar-pc-right-almanac {
  border-top-color: #eb7563;
}

.op-calendar-pc-holidaystyle .op-calendar-hover-almanac {
  border-color: #cb1c18;
}
